}
* {
  transition: background-color var(--transition-fast), color var(--transition-fast), border-color var(--transition-fast), fill var(--transition-fast);
}
html, body {
  height: 100%;
}
body {
  background: var(--bg);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1rem;
}
.card {
  background: linear-gradient(180deg, var(--surface), var(--surface));
  @supports (color: color-mix(in lab, red, red)) {
    background: linear-gradient(180deg, color-mix(in srgb, var(--surface) 92%, transparent 0%), var(--surface));
  }
  border: 1px solid var(--border);
  color: var(--foreground);
  border-radius: 12px;
  box-shadow: 0 1px 2px rgba(2,6,23,0.04);
  transition: background-color var(--transition-fast), color var(--transition-fast), border-color var(--transition-fast), box-shadow var(--transition-fast);
}
table {
  width: 100%;
  border-collapse: collapse;
  color: var(--foreground);
}
thead th {
  text-align: left;
  padding: 0.6rem 0.75rem;
  font-weight: 600;
  background: var(--surface);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--surface) 80%, transparent 0%);
  }
  border-bottom: 1px solid var(--border);
}
tbody td {
  padding: 0.55rem 0.75rem;
  border-bottom: 1px solid var(--border);
}
tbody tr:nth-child(even) {
  background: var(--foreground);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--foreground) 4%, transparent 0%);
  }
}
.table-excel {
  border: 1px solid var(--border);
  border-radius: 8px;
  overflow: hidden;
}
.table-excel thead th {
  background: linear-gradient(180deg, var(--surface), var(--surface));
  @supports (color: color-mix(in lab, red, red)) {
    background: linear-gradient(180deg, color-mix(in srgb, var(--surface) 88%, transparent 0%), var(--surface));
  }
  color: var(--foreground);
  padding: 0.75rem 0.9rem;
  text-align: left;
  font-weight: 700;
  letter-spacing: 0.01em;
}
.table-excel tbody td {
  padding: 0.6rem 0.9rem;
  border-bottom: 1px solid var(--border);
}
.table-excel tbody tr:nth-child(even) {
  background: var(--foreground);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--foreground) 3%, transparent 0%);
  }
}
.table-excel tbody tr:hover {
  background: var(--accent);
  @supports (color: color-mix(in lab, red, red)) {
    background: color-mix(in srgb, var(--accent) 10%, transparent 0%);
  }
  transition: background-color var(--transition-fast);
}
.table-small {
  font-size: 0.92rem;
}
.muted {
  color: var(--muted);
}
.muted-text {
  color: var(--muted);
  font-size: 0.875rem;
}
.btn {
  background: var(--accent);
  color: white;
  padding: 0.45rem 0.9rem;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: background-color var(--transition-fast), transform 120ms ease;
}
.btn:active {
  transform: translateY(1px);
}
.btn-small {
  background: transparent;
  border: 1px solid var(--border);
  color: var(--foreground);
  padding: 0.25rem 0.45rem;
  border-radius: 8px;
  font-size: 0.9rem;
  cursor: pointer;
  height: 36px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.4rem;
  box-sizing: border-box;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.controls-row {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  margin-bottom: 0.75rem;
}
.controls-row .group:first-child {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 0.5rem;
  flex: 1 1 auto;
  width: 100%;
}
.controls-row .group:not(:first-child) {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.controls-row .group:first-child button,
.controls-row .group:first-child .btn-small {
  width: 100%;
  height: 40px;
  min-width: 0;
}
@media (max-width: 720px) {
  .controls-row .group:first-child {
    grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
    gap: 0.4rem;
  }
  .controls-row .group:first-child button,
  .controls-row .group:first-child .btn-small {
    height: 36px;
  }
}
.btn-small:focus, .controls-row button:focus, .btn:focus {
  outline: 3px solid rgba(11,99,214,0.22);
  outline-offset: 2px;
}
